.header-img {
    margin-top: 1rem;
    height: 30rem;
    background: url("http://curly.mikado-themes.com/wp-content/uploads/2018/04/h1-slider3-background-img.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center; 
    box-shadow: inset 0px 43px 50px -5px rgba(255,255,255,1);
}

.h {
  text-align: center;
  padding: 1rem 0;
  letter-spacing: 0.4em;
  font-weight: bold;
  h2:after {
      content: '';
      display: block;
      width: 7rem;
      height: 0.1rem;
      background: #C79438;
      margin: 0 auto;
      margin-top: 1rem;
  }
}

.services {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 20rem;
    

    .highlights {
        flex-basis: 33.33%;
        display: block;
        text-align: center;
        padding: 2rem;
        height: 100%;
        .inner-div {
            height: 100%;
            padding: 2rem;
            img {
                padding-top: 1rem;
            }
            h3 {
                font-size: 2.5em;
                letter-spacing: 0.15em;
            }
            a {
                color: #C79438;
                font-weight: bold;
                text-transform: uppercase;
                font-size: 0.7em;
                letter-spacing: 0.25em;
                
            }
        }
    }

    .care {
        flex-basis: 33.33%;
        display: block;
        text-align: center;
        background: black;
        height: 100%;
        color: white;
        padding: 2rem;
        .inner-div2 {
            height: 100%;
            padding: 2rem;
            img {
                padding-top: 1rem;
            }
            h3 {
                font-size: 2.5em;
            }
            a {
                color: #C79438;
                font-weight: bold;
                text-transform: uppercase;
                font-size: 0.7em;
                letter-spacing: 0.25em;
               
            }
        }
    }

    .haircut {
        flex-basis: 33.33%;
        display: block;
        text-align: center;
        padding: 2rem;
        height: 100%;
        .inner-div3 {
            height: 100%;
            padding: 2rem;
            img {
                padding-top: 1rem;
            }
            h3 {
                font-size: 2.5em;
                letter-spacing: 0.15em;
            }
            a {
                color: #C79438;
                font-weight: bold;
                text-transform: uppercase;
                font-size: 0.7em;
                letter-spacing: 0.25em;
            }
        }
    }

    .highlights:hover, .care:hover, .haircut:hover {
        .inner-div, .inner-div2, .inner-div3 {
            border: 2px solid #C79438;
            transition: all .2s ease-in-out;
            transform: scale(0.95);
        }
    }
}

.subscribe {
    background: #141414;
    form {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5rem;
        input {
            width: 25rem;
            background: transparent;
            padding: 1rem;
            font-weight: bold;
            border: 2px solid white;
            color: white;
            outline: none;
            letter-spacing: 0.25em;
            }
        input:focus {
            border: 2px solid #C79438;
        }    
        .btnSubscribe {
            width: 8rem;
            text-transform: uppercase;
            background: white;
            color: black;
            font-weight: bold;
            border: none;
            outline: none;
        }      
        .btnSubscribe:hover {
            background: #C79438;
        }
    }
}

.container-team {
    display: flex;
    height: 100%;
    padding: 3rem;
    justify-content: space-between;
   
}
.our-team{
    padding: 20px 15px 30px;
    background: white;
    border-radius: 15px;
    text-align: center;
    box-shadow: 0px 0px 40px -5px rgba(0,0,0,0.66);
}
.our-team .pic{
    display: inline-block;
    width: 15rem;
    height: 15rem;
    background: white;
    padding: 10px;
    margin-bottom: 25px;
    transition: all 0.5s ease 0s;
}
.our-team:hover .pic{
    background: #C79438;
    border-radius: 50%;
}
.pic img{
    width: 100%;
    height: auto;
    border-radius: 50%;
}
.our-team .title{
    display: block;
    font-size: 20px;
    font-weight: 600;
    color: #2e282a;
    text-transform: uppercase;
    margin: 0 0 7px 0;
}
.our-team .post{
    display: block;
    font-size: 15px;
    color: #C79438;
    text-transform: capitalize;
    margin-bottom: 15px;
}
.our-team .social{
    padding: 0;
    margin: 0;
    list-style: none;
}
.our-team .social li{
    display: inline-block;
    margin-right: 5px;
}
.our-team .social li a{
    display: block;
    width: 30px;
    height: 30px;
    line-height: 30px;
    border-radius: 50%;
    font-size: 15px;
    color: #C79438;
    border: 1px solid #C79438;
    transition: all 0.5s ease 0s;
    text-decoration: none;
}
.our-team:hover .social li a{
    background: #C79438;
    color: #fff;
}
@media only screen and (max-width: 990px){
    .our-team{ margin-bottom: 30px; }
}